@import url("https://fonts.googleapis.com/css?family=Roboto&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  outline: 0;
  list-style-position: inside;
  user-select: none;
  word-break: break-all;
}

body {
  font-size: 16px;
  font-family: 'Roboto', arial;
}

body::-webkit-scrollbar {
  width: 12px;
}

body::-webkit-scrollbar-track {
  background-color: #666;
}

body::-webkit-scrollbar-thumb {
  background-color: #ccc;
}

.container {
  max-width: 1280px;
  padding: 10px;
  margin: 0 auto;
}

.title {
  width: 100%;
  font-size: 30px;
  color: #333;
  margin-bottom: 20px;
  margin-top: 10px;
  padding-left: 10px;
  border-left: 6px solid #D91624;
  letter-spacing: 2px;
}

.product {
  display: flex;
  flex-flow: column;
  justify-content: space-between;
  background: #fff;
  padding: 10px;
  position: relative;
}

.product .product_cover {
  display: block;
  width: 100%;
}

.product .product_cover img {
  width: 100%;
  max-height: 160px;
  min-height: 160px;
  overflow: hidden;
  object-fit: cover;
}

.product h4 {
  margin-bottom: 5px;
}

.product h4 a {
  text-decoration: none;
  color: #5d5d5d;
}

.product #short {
  color: #949494;
  margin-bottom: 7px;
  font-size: 15px;
}

.product #old_price {
  font-size: 13px;
  color: #5d5d5d;
  text-decoration: line-through;
}

.product #price {
  color: #D91624;
  margin-bottom: 7px;
}

.product .buy {
  display: block;
  padding: 7px 20px;
  background: #5DAD42;
  color: #fff;
  text-decoration: none;
  text-align: center;
  font-size: 14px;
  transition: 0.5s;
}

.product .buy:hover {
  background: #62B746;
}

.product:hover h4 a {
  text-decoration: underline;
}

.sale_overlay,
.new_overlay {
  position: absolute;
  top: 0;
  font-size: 13px;
  padding: 5px;
  color: #fff;
}

.sale_overlay {
  background: #D91624;
  left: 0;
}

.new_overlay {
  background: #5DAD42;
  right: 0;
}

.btn-up {
  position: fixed;
  color: white;
  right: 10px;
  bottom: 50px;
  width: 50px;
  height: 50px;
  text-align: center;
  line-height: 50px;
  cursor: pointer;
  background: #D91624;
  display: none;
  font-size: 30px;
  z-index: 3500;
  transition: all .2s;
}

.btn-up:hover {
  background: #E31725;
  line-height: 40px;
}

.btn-up svg {
  fill: #fff;
}

.swiper-button-next {
  right: 15%;
}

.swiper-button-prev {
  left: 15%;
}

.hover {
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 5px 0 rgba(0, 0, 0, 0.12), 0 3px 1px -2px rgba(0, 0, 0, 0.2);
  transition: box-shadow .25s;
}

.hover:hover {
  box-shadow: 0 8px 17px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.bigSlider {
  max-height: 405px;
  overflow: hidden;
}

.bigSlider .swiper-container .swiper-wrapper .swiper-slide img {
  width: 100%;
}

.bigSlider .swiper-container .swiper-button-prev,
.bigSlider .swiper-container .swiper-button-next {
  opacity: 0;
  transition: 0.5s;
}

.bigSlider:hover .swiper-container .swiper-button-prev,
.bigSlider:hover .swiper-container .swiper-button-next {
  opacity: 1;
}

a {
  color: #2e89ff;
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

.wrap {
  width: 100%;
  position: relative;
  height: 0;
  overflow: hidden;
  padding-bottom: 56.25%;
}

.wrap iframe,
.wrap object,
.wrap embed {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
}

.see_more {
  display: block;
  margin: 10px 0;
}

.stick {
  position: fixed;
  width: 100%;
  top: 0;
  z-index: 9999;
}

.custom {
  display: inline-block;
  width: 40%;
  position: relative;
  padding-left: 25px;
  margin-right: 15px;
  margin-bottom: 7px;
  cursor: pointer;
  font-size: 14px;
  user-select: none;
}

.custom input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}

.custom .checkmark {
  position: absolute;
  top: 0;
  left: 0;
  height: 18px;
  width: 18px;
  background-color: #ffe;
  border: 1px solid #ccc;
}

.custom .checkmark:after {
  content: "";
  position: absolute;
  display: none;
}

.custom:hover input ~ .checkmark {
  background-color: #E31725;
  border-color: #E31725;
}

.custom input:checked ~ .checkmark {
  background-color: #D91624;
  border-color: #D91624;
}

.custom input:checked ~ .checkmark:after {
  display: block;
}

.custom .checkmark:after {
  left: 4px;
  top: 1px;
  width: 5px;
  height: 9px;
  border: solid white;
  border-width: 0 3px 3px 0;
  transform: rotate(45deg);
}

.pagination {
  grid-column-end: 3;
  text-align: center;
  width: 100%;
  margin-bottom: 20px;
  margin-top: 10px;
}

.pagination a {
  color: #fff;
  background: #D91624;
  padding: 7px 10px;
  font-size: 14px;
}

.pagination a:hover {
  background: #E31725;
}

.lg-backdrop {
  z-index: 9999 !important;
}

.lg-outer {
  z-index: 9999 !important;
}

.basket_items {
  text-align: left;
}

.basket_items li {
  list-style-type: none;
}

.basket_items li a {
  display: inline-block;
  margin-right: 10px;
  margin-bottom: 10px;
  font-size: 15px;
}

.basket_items li svg {
  width: 20px;
  height: 20px;
  margin-left: 15px;
  cursor: pointer;
}

.basket_items p {
  font-size: 15px;
  font-weight: bold;
  margin-top: 10px;
}

.basket_items .toBasketPage {
  color: #fff;
  display: block;
  text-align: center;
  margin-top: 15px;
  background: #5DAD42;
  font-size: 15px;
  padding: 9px;
  text-decoration: none;
  transition: 0.5s;
}

.basket_items .toBasketPage:hover {
  background: #62B746;
}

header .header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 900px;
}

header .header .logo a {
  display: block;
  max-width: 120px;
}

header .header .logo a img {
  width: 100%;
}

header .header .search_tel .header_tel {
  display: flex;
  align-items: center;
}

header .header .search_tel .header_tel a {
  text-decoration: none;
  font-size: 26px;
  color: #333;
}

header .header .search_tel .header_tel p {
  font-size: 12px;
  margin-left: 13px;
}

header .header .search_tel form {
  display: flex;
  margin-top: 4px;
}

header .header .search_tel form input {
  padding: 7px;
  width: 100%;
  background: #eee;
  border: 1px solid #ccc;
}

header .header .search_tel form input:focus {
  background: unset;
}

header .header .search_tel form button {
  border: none;
  background: #D91624;
  padding: 3px 6px;
  cursor: pointer;
  transition: 0.3s;
}

header .header .search_tel form button svg {
  width: 20px;
  height: 20px;
}

header .header .search_tel form button:hover {
  background: #E31725;
}

header .header .lang button {
  border: none;
  background: none;
  font-size: 16px;
  padding: 7px;
  cursor: pointer;
}

header .header .lang button.active {
  background: #D91624;
  color: #fff;
}

header .header .lang button:hover {
  background: #E31725;
  color: #fff;
}

header .header .basket {
  cursor: pointer;
  position: relative;
}

header .header .basket .basket_count_preview {
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  color: #fff;
  font-size: 12px;
  padding: 3px;
  width: 20px;
  height: 20px;
  border-radius: 50% 70%;
  top: 19px;
  right: 15px;
}

header .header .basket svg {
  width: 50px;
  height: 50px;
  fill: #D91624;
  stroke: #D91624;
}

