nav {
  background: #E9E9E9;
  box-shadow: 0px 8px 15px 2px #ccc;
}

nav .nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0px 10px;
  z-index: 9999;
  max-width: 1000px;
}

nav .nav .menu-btn {
  display: none;
  cursor: pointer;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  position: relative;
  z-index: 3;
}

nav .nav .menu-btn .menu-btn span,
nav .nav .menu-btn .menu-btn span::before,
nav .nav .menu-btn .menu-btn span::after {
  position: absolute;
  top: 50%;
  margin-top: -1px;
  left: 50%;
  margin-left: -20px;
  width: 40px;
  height: 2px;
  background-color: #D91624;
}

nav .nav .menu-btn .menu-btn span::before,
nav .nav .menu-btn .menu-btn span::after {
  content: '';
  display: block;
  transition: 0.5s;
}

nav .nav .menu-btn .menu-btn span::before {
  transform: translateY(-10px);
}

nav .nav .menu-btn .menu-btn_active span {
  height: 0;
}

nav .nav .menu-btn .menu-btn span::after {
  transform: translateY(10px);
}

nav .nav .menu-btn .menu-btn_active span:before {
  transform: rotate(-45deg);
  transform-origin: center;
}

nav .nav .menu-btn .menu-btn_active span:after {
  transform: rotate(45deg);
  transform-origin: center;
}

nav .nav ul {
  display: flex;
}

nav .nav ul li {
  list-style-type: none;
}

nav .nav ul li a {
  display: flex;
  align-items: center;
  text-decoration: none;
  padding: 10px;
  color: #333;
  min-height: 60px;
}

nav .nav ul li a:hover {
  background: #E31725;
  color: #fff;
}

nav .nav ul form {
  display: none;
}

nav .nav #location {
  display: flex;
  align-items: center;
  text-decoration: underline;
  cursor: pointer;
}

nav .nav #location svg {
  width: 40px;
  height: 40px;
}

